import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';
import { tokenNotExpired } from 'angular2-jwt';

@Injectable()
export class AuthService {
  authToken: any;

  constructor(
    private http: Http) { }

  getWithAuth(address){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.get(address, {headers: headers})
      .map(res => res.json());
  }

  registerUser(user){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('users/register', user, {headers: headers})
      .map(res => res.json());
  }

  editUserProfile(user, _id){
    let headers = new Headers();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.put('users/profile/edit/' + _id, user, {headers: headers})
      .map(res => res.json());
  }

  authenticateUser(user){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('users/authenticate', user, {headers: headers})
      .map(res => res.json());
  }


  getProfile(){
    return this.getWithAuth('users/profile');
  }

  getCustomers(){
    return this.getWithAuth('users/show/customers');
  }

  getStaff(){
      return this.getWithAuth('users/show/staff');
  }

  loadToken(){
    const token = localStorage.getItem('token');
    this.authToken = token;
  }

  storeUserData(token){
    localStorage.clear();
    localStorage.setItem('token', token);
  }

  loggedIn(){
    return tokenNotExpired();
  }

  logout(){
    this.authToken = null;
    localStorage.clear();
  }
}



